<template>
  <div>
    <mrc_ess_table v-if="$route.name=='organizationalManagement'"
                   :headers='headers'
                   :hideAddBtn='!hasBtn("add-organization-change")'
                   pageName='addOrganizationalManagement'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['name','result_status','changeeffective']"
                   :items='desserts'>

      <template v-slot:top>
        <v-card-title style="height:60px;position:relative;">
          <mrc_ess_searchInput :isShow='true'
                               style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput>
        </v-card-title>
      </template>

      <template v-slot:item.name='{item}'>
        <mrc_ess_staffSpan :list='item'
                           class="mt-3 mb-3"></mrc_ess_staffSpan>
      </template>

      <template #item.changeeffective='{item}'>
        <span>{{item.Change_effective|changeDate}}</span>
      </template>
      <!-- 状态 -->
      <template v-slot:item.result_status='{item}'>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.result_status==''">{{$t('label_Unchanged')}}</v-btn>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.result_status==1">{{$t('label_Complete')}}</v-btn>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.result_status==2">{{$t('label_The_task_processing_is_complete')}}</v-btn>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon v-btnAuth="['view-organization-change',
                                'edit-organization-change',
                                'delete-organization-change']">mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import {
  getOrganizationalManagementList
  , detailOrganizationalManagement
  , addOrganizationalManagement
  , updateOrganizationalManagement
  , deleteOrganizationalManagement
} from '@/service/api/OrganizationalManagement.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      stepIndex: 1,
      dialog: false,
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      desserts: [],
    };
  },
  computed: {
    stepList () {
      return [
        {
          name: this.$t('label_Importing_files')
        },
        {
          name: this.$t('label_result')
        },
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View'), show: this.hasBtn('view-organization-change') },
        { name: this.$t('label_Edit'), show: this.hasBtn('edit-organization-change') },
        { name: this.$t('label_delete'), show: this.hasBtn('delete-organization-change') },
      ]
    },
    headers () {
      return [
        { text: this.$t('label_The_original_department'), align: 'start', value: 'name', sortable: false },
        { text: this.$t('label_changeStatus'), value: 'staff_post_uuid', sortable: false },
        { text: this.$t('label_New_department'), value: 'changeeffective', sortable: false, },
        { text: this.$t('label_Effective_date'), value: 'changeeffective', sortable: false, },
        { text: this.$t('label_The_execution_status'), value: 'result_status', sortable: false, },
        { text: '', value: 'actions', sortable: false, align: 'end', },
      ]
    },
  },
  created () { },
  watch: {
    $route (v) {
      if (v.name == 'organizationalManagement') {
        this.getlist()
      }
    }
  },
  mounted () {
    this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    changeIndex (v) {
      if (v == 0) {
        this.dialog = false
      }
      else if (v == 3) { //提交代码逻辑

      }
      else {
        this.stepIndex = v
      }
    },
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getOrganizationalManagementList({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    addStaffLeave () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addOrganizationalManagement'
      })
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      // this.$store.commit('set_subPage_name', item.job_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailOrganizationalManagement'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          // this.$store.commit('set_subPage_name', item.job_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailOrganizationalManagement'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteOrganizationalManagement({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                if (this.desserts.length = 1) this.options.cpage--
                this.getlist()
              }
            })
          })
          break
      }
    },
    success () { },
    cancel () { }
  },
};
</script>

